// Color Palette
$primary-color: #2D3747;        // Deep navy blue
$accent-color: #008753;         // Green from hero gradient
$accent-secondary: #012169;     // Blue from hero gradient
$background-light: #F7FAFC;     // Light gray background
$text-dark: #2D3747;           // Dark text
$text-light: #CBD5E0;          // Light text
$text-muted: #718096;          // Muted text

// Hero gradient colors
$hero-green: #008753;
$hero-blue: #012169;

// Hobby-specific colors (from your data)
$anime-color: #F6F8FF;
$sneakers-color: #FFE5B4;
$gym-color: #E8F5E9;

// Typography
$font-primary: 'Barlow', sans-serif;    // Headings
$font-secondary: 'Inter', sans-serif;   // Body text

// Font weights
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;


// Spacing System
$spacing-xxs: 0.25rem;  // 4px
$spacing-xs: 0.5rem;    // 8px
$spacing-sm: 1rem;      // 16px
$spacing-md: 1.5rem;    // 24px
$spacing-lg: 2rem;      // 32px
$spacing-xl: 3rem;      // 48px
$spacing-xxl: 4rem;     // 64px


// Breakpoints
$breakpoint-mobile: 768px;
$breakpoint-tablet: 1024px;
$breakpoint-desktop: 1200px;


// Shadows & Effects
$shadow-sm: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
$shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
$shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
$shadow-xl: 0 20px 40px rgba(0, 0, 0, 0.15);
$shadow-card: 0 8px 25px rgba(0, 0, 0, 0.06);
$shadow-card-hover: 0 12px 35px rgba(0, 0, 0, 0.1);
$shadow-accent: 0 4px 15px rgba($accent-color, 0.3);
$shadow-accent-hover: 0 8px 25px rgba($accent-color, 0.4);

// Border Radius Scale
$radius-sm: 8px;
$radius-md: 16px;
$radius-lg: 24px;
$radius-pill: 50px;
$radius-full: 9999px; // Full rounded (for circles and pills)

// Transitions
$transition-fast: 0.2s ease;
$transition-medium: 0.3s ease;
$transition-slow: 0.5s ease;